/* ------------------------------------------------------------------------------------------------ */
/* Form */
/* ------------------------------------------------------------------------------------------------ */

.form-group {
  label {
    color: #000000;
    i {
      color: $primary; } }
  small {
    font-size: 14px;
    line-height: 20px;
    color: #767676; }
  label + small {
    display: block;
    margin-top: -4px;
    margin-bottom: 16px; } }


label {
  font-size: 16px;
  color: #A0A0A0;
  margin-bottom: 10px;
  display: block;
  &.inline {
    margin-bottom: 0; } }

.form-control {
  height: 50px;
  border-color: #c3c3c4;
  box-shadow: 4px 4px 8px #6C6C6C0F;
  border-radius: 8px;
  position: relative;
  &::placeholder {
    font-size: 14px;
    line-height: 1.2em;
    color: #A8A8A8; } }

textarea.form-control {
  padding: 16px 21px; }

.date {
  position: relative;
  .date-box {
    position: relative;
    display: inline-block;
    &:after {
      content: "";
      width: 17px;
      height: 17px;
      position: absolute;
      right: 20px;
      top: 15px;
      background-image: url('../images/ic-date.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center; } } }



.uploadfile {
  .inputfile {
    opacity: 0;
    visibility: hidden;
    width: 0;
    height: 0; } }

.currency {
  position: relative;
  max-width: 260px;
  .form-control {
    padding-left: 50px; }
  .crncy {
    font-size: 16px;
    line-height: 1.2em;
    position: absolute;
    top: 50px;
    left: 20px;
    color: #767676; } }

// BS Select
.select {
  &.inline {
    display: inline-block;
    vertical-align: middle;
    margin-left: 8px;
    min-width: 0;
    width: auto; } }
.dropdown.bootstrap-select {
  box-shadow: 4px 4px 8px #6C6C6C0F;
  min-width: 250px;
  width: auto !important;
  max-width: 350px;
  .btn.dropdown-toggle {
    font-size: 14px;
    // line-height: 1.5em
    line-height: 24px;
    height: 50px;
    background: #fff;
    border: 1px solid #c3c3c4;
    font-weight: 400;
    &:focus {
      outline: none !important; } } }

// Select2
.selectAlt {}
.select2 {}
.select2-selection {}

.form {
  label {
    font-size: 16px;
    line-height: 1.5em; } }

.form-group {
  position: relative;
  .err-msg {
    position: absolute;
    left: 0;
    bottom: -24px;
    .error {
      font-size: 12px;
      line-height: 1.5em;
      color: #F23C13;
      font-weight: 400;
      margin-bottom: 0; } }
  .counter {
    position: absolute;
    right: 20px;
    bottom: 15px;
    font-size: 14px;
    line-height: 1.2em;
    color: #767676;
    display: none;

    .cntr + .max {
      &:before {
        content: " / ";
        display: inline-block;
        vertical-align: middle;
        padding: 0 5px; } } } }


hr.divider {
  display: block;
  width: 100%;
  max-width: 630px;
  background-color: #D5D5D5;
  display: block;
  margin: 40px 0; }

.form-footer {
  p.note {
    font-size: 14px;
    line-height: 20px;
    color: $primary; } }

.group-button {
  width: 100%;
  display: flex;
  justify-content: space-between;

  .btn {
    padding: 10px 32px; } }

.custom-control {
  padding-left: 30px;
  &.custom-radio {}
  &-inline {
    margin-right: 45px; }
  &-label {
    &:before,
    &:after {
      width: 20px;
      height: 20px;
      margin-top: -3px;
      left: -30px; }
    &:before {
      border: 2px solid #000; } } }

.custom-control-input:checked ~ .custom-control-label::before {
  border-color: $primary;
  background-color: $primary; }
