/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

h1.logo {
  text-align: center;
  margin-bottom: 46px;
  .header__logo {
    height: 24px;
    display: inline-block;
    margin: 0 auto;
    img {
      display: block;
      width: auto;
      max-height: 24px; } } }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 18px 36px;
  margin-left: 205px;
  background: #F6F6F7;
  box-shadow: 4px 4px 8px #6C6C6C0F;
  z-index: 99;
  &-container {
    max-width: calc(100% - 205px); }

  .row {
    align-items: center; }
  .col.right {
    display: flex;
    justify-content: flex-end; }

  .write-brief {
    .btn-brief {
      padding: 9px 15px;
      padding-left: 55px;
      position: relative;
      color: $primary;
      font-size: 16px;
      line-height: 24px;
      background: #F5D9D4;
      border: 0.2px solid var(--unnamed-color-f23c13);
      border-radius: 8px;
      font-weight: 500;
      text-decoration: none;
      display: inline-block;
      &:before {
        content: "";
        display: block;
        width: 40px;
        height: 40px;
        border-radius: 8px;
        background: $primary;
        background-image: url('../images/ic-plus-btn.png');
        background-position: center;
        background-size: 14px;
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
        left: 0; } } }

  .user-header {
    &>ul {
      margin-left: -15px;
      margin-right: -15px;
      &>li {
        padding: 0 15px;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        & > a {
          display: inline-block;
          vertical-align: middle;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          & > img {
            display: block;
            max-width: 100%;
            height: 100%; } } } }
    .notif {
      a {
        transition: all .3s ease; }
      &:hover {
        a {
          transform: rotate(10deg); } } }
    .user-acnt {
      & > a {
        border: 2px solid #EDEDED; }
      &:hover {
        & > a {
          border-color: #000; } } }

    .user-acnt_menu {
      border-radius: 8px;
      background: #fff;
      box-shadow: 4px 4px 8px #6C6C6C0F;
      border: 1px solid #EDEDED;
      position: absolute;
      min-width: 326px;
      padding: 12px;
      top: 50px;
      right: 12px;
      opacity: 0;
      visibility: hidden;
      transition: all .5s ease;
      overflow: hidden;
      ul {
        li {
          margin-bottom: 8px;
          &:last-child {
            margin-bottom: 0; }
          a {
            display: block;
            padding: 10px;
            color: $text;
            border-radius: 8px;
            text-decoration: none; }
          .ic {
            display: inline-block;
            vertical-align: middle;
            width: 15px;
            height: 15px;
            margin-right: 10px;
            img {
              display: block;
              width: 100%;
              height: 100%;
              object-fit: contain; } }
          &:hover {
            a {
              background: #F4D9D3; } } } } } }

  &.no-sidebar {
    margin-left: 0;
    .header-container {
      max-width: 100%; } }

  h1.logo {
    text-align: left;
    margin-bottom: 0;
    line-height: 0em; } }

.usracnt-open {
  .header .user-header {
    .user-acnt_menu {
      top: 40px;
      opacity: 1;
      visibility: visible; } } }



// Sidebar Menu
.sidebar {
  padding: 24px 12px;
  background: #fff;
  display: block;
  width: 205px;
  height: 100vh;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  background: #fff;
  h1.logo {
    text-align: center;
    margin-bottom: 46px;
    .header__logo {
      height: 24px;
      display: inline-block;
      margin: 0 auto;
      img {
        display: block;
        width: auto;
        max-height: 24px; } } } }

.sidemenu {
  ul {
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #EDEDED;
    li {
      margin-bottom: 4px;
      a {
        display: block;
        padding: 12px 20px;
        border-radius: 8px;
        color: $text;
        font-size: 14px;
        line-height: 20px;
        font-weight: 500;
        text-decoration: none;
        .icon {
          width: 20px;
          height: 20px;
          margin-right: 14px;
          display: inline-block;
          vertical-align: middle;
          img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
            &.hover {
              display: none; } } }
        .count {
          font-size: 14px;
          line-height: 20px;
          padding: 0 6px;
          background: $primary;
          color: #fff;
          border-radius: 4px;
          display: inline-block;
          margin-left: 8px; } }
      &:hover,
      &.active,
      &.current-menu-item {
        a {
          background: #F4D9D3;
          color: $primary;
          .icon {
            img:not(.hover) {
              display: none; }
            img.hover {
              display: block; } } } } } } }

.dontknow-box {
  padding: 24px 15px;
  padding-left: 20px;
  background: #F6F6F6;
  border-radius: 8px;
  .icon {
    width: 34px;
    height: 34px;
    border-radius: 5px;
    margin-bottom: 14px;
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain; } }
  h4 {
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    color: $primary;
    margin-bottom: 18px; }
  a {
    font-size: 12px;
    line-height: 18px;
    color: $text; } }


.needhelp {
  position: absolute;
  width: 100%;
  left: 12px;
  bottom: 24px;
  text-align: center;
  .img {
    width: 80px;
    height: 53px;
    margin: 0 auto 4px;
    img {
      display: block;
      width: 100%;
      height: 100%; } }
  a {
    color: $text; } }
