main {
  background: #F6F6F7; }

.inline {
  display: inline-block;
  vertical-align: middle;
  width: auto; }

.btn {
  font-size: 16px;
  line-height: 24px;
  padding: 10px 24px;
  border-radius: 8px;
  font-weight: 600;
  transition: all .3s ease;
  &:hover {
    text-decoration: none; }
  &:focus, &.focus {
    outline: none !important;
    box-shadow: none; }
  &.disabled,
  &:disabled {
    opacity: 1; }
  .icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    &:first-child {
      margin-right: 8px; }
    &:last-child {
      margin-left: 8px; } }
  span {
    display: inline-block; }

  & + .btn {
    margin-left: 24px; }

  // BTN PRIMARY
  &-primary {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:hover {
    color: #FFF;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:focus, &-primary.focus {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%);
    box-shadow: none; }
  &-primary.disabled, &-primary:disabled {
    color: #FFF;
    background-color: #A8A8A8;
    border-color: #A8A8A8; }
  &-primary:not(:disabled):not(.disabled):active,
  &-primary:not(:disabled):not(.disabled).active,
  .show > &-primary.dropdown-toggle {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:not(:disabled):not(.disabled).active:focus,
  &-primary:not(:disabled):not(.disabled):active:focus,
  .show > &-primary.dropdown-toggle:focus {
    box-shadow: none; }


