.btn-link-black {
  color: $text;
  text-decoration: underline;
  transition: all .3s ease;
  &:hover {
    color: $secondary; } }

.btn-upload {
  border: 1px solid #ced4da;
  font-size: 16px;
  line-height: 1em;
  font-weight: 400;
  color: $primary !important;
  margin-top: 28px;
  min-width: 255px;
  position: relative;
  text-align: left;
  &:after {
    content: "";
    width: 18px;
    height: 18px;
    background-image: url('../images/ic-upload-file.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 12px;
    right: 20px; } }

.link-primary {
  color: $primary;
  transition: all .3s ease;
  &:hover {
    color: $primary;
    text-decoration: underline; } }

.accordion {
  > .card {
    position: relative;
    border: 0;
    border-radius: 30px !important;
    margin-bottom: 30px;
    overflow: hidden;
    background: #F6F6F6;
    &:before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 80%;
      left: 0;
      border-radius: 30px;
      background: #F6F6F6;
      opacity: 0;
      transition: all .3s ease-in-out; }
    &:last-child {
      margin-bottom: 0; }
    &.open:before {
      top: 0;
      opacity: 1;
      transition-delay: .1s; }
    > .card-header {
      position: relative;
      z-index: 2;
      color: #BFBFBF;
      cursor: pointer;
      border-radius: 30px;
      border: 0;
      background: transparent;
      padding: 32px 90px 32px 32px;
      font-size: 18px;
      line-height: 1.5em;
      font-weight: 500;
      transition: all .3s ease .2s;
      margin: 0;
      .chevron {
        position: absolute;
        right: 32px;
        display: block;
        width: 24px;
        top: calc(50% - 12px);
        transform: none;
        transition: all .3s ease;
        .svg {
          display: block;
          width: 24px;
          height: 24px;
          path {
            fill: #BFBFBF !important; } } }
      &:not(.collapsed) {
        border-radius: 30px 30px 0 0;
        color: $text;
        .chevron {
          transform: rotateX(180deg);
          .svg path {
            fill: #FFFFFF !important; } }
        &:before {
          opacity: 1; } } }
    .card-body {
      position: relative;
      z-index: 2;
      color: #565656;
      background: transparent;
      border-radius: 0 0 30px 30px;
      padding: 10px 32px 32px;
      transition: all .3s ease;
      margin: 0; } }
  &--klien {
    > .card {
      &:before {
        background: $klien; }
      > .card-header:not(.collapsed) {
        color: #FFF; }
      .card-body {
        color: #FFF; } } } }


// Modal
.modal {
  &-dialog {
    min-height: calc(100% - (1.75rem * 2));
    display: flex;
    align-items: center; } }
