/* ------------------------------------------------------------------------------------------------ */
/* Content */
/* ------------------------------------------------------------------------------------------------ */
.client-content {
  margin-top: 40px;
  width: calc(100% - 205px);
  max-width: 1088px;
  margin: 50px auto;
  margin-bottom: 0;
  margin-left: 250px; }

.page-title {
  margin-bottom: 16px;
  h2 {
    font-size: 36px;
    line-height: 1.2em;
    font-weight: 600;
    margin-bottom: 0; } }

section {
  margin-bottom: 54px;
  &:last-child {
    margin-bottom: 0; } }

.section_title {
  position: relative;
  margin-bottom: 24px;
  h3 {
    font-size: 18px;
    line-height: 32px;
    font-weight: 600; }
  .show-more {
    position: absolute;
    top: 0;
    right: 0;
    a {
      color: $text;
      &:after {
        content: ">";
        display: inline-block;
        vertical-align: middle;
        margin-left: 9px; } } } }

.client {
  &-home {
    .welcome-banner {
      padding: 60px 66px;
      background: #fff;
      border: 1px solid #EDEDED;
      box-shadow: 4px 4px 8px #6C6C6C0F;
      border-radius: 8px;
      position: relative;
      margin-bottom: 54px;
      .text {
        max-width: 510px; }
      h2 {
        font-size: 32px;
        line-height: 42px;
        font-weight: 700;
        color: $primary;
        margin-bottom: 16px;
        img {
          max-height: 38px;
          display: inline-block;
          vertical-align: middle; } }
      p {
        color: $text-light;
        font-size: 18px;
        line-height: 26px; }
      .img {
        width: 440px;
        height: 325px;
        object-fit: cover;
        position: absolute;
        top: -35px;
        right: 12px; }
      .btn-primary {
        &:before {
          content: "";
          width: 12px;
          height: 12px;
          display: inline-block;
          background-image: url(../images/ic-plus-btn.png);
          background-size: contain;
          background-position: center;
          background-repeat: no-repeat;
          margin-left: -10px;
          margin-right: 10px; } } } } }

.progress-bar {
  background-color: transparent;
  display: block;
  margin-bottom: 0;
  .pb {
    text-align: left;
    &-block {
      display: inline-block;
      width: 100%;
      height: 12px;
      background: #FFFFFF;
      border-radius: 10px;
      position: relative;
      overflow: hidden;
      span {
        position: absolute;
        height: 12px;
        top: 0;
        left: 0;
        background: $primary;
        border-radius: 10px; } }
    .prcntg {
      display: inline-block;
      vertical-align: middle;
      font-size: 16px;
      line-height: 22px;
      font-weight: bold;
      margin-left: 8px;
      color: #4C9F70; } }
  p {
    font-size: 18px;
    line-height: 32px;
    color: $primary;
    text-align: left;
    font-weight: 600;
    margin-bottom: 0; }
  &.rest {
    .pb {
      &-block {
        span {
          background: #ffbb00; } } }
    .prcntg {
      color: #ffbb00; } } }

.card-white {
  padding: 30px 24px;
  background: #FFF;
  border: 1px solid #EDEDED;
  border-radius: 8px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  // cursor: pointer
  position: relative;
  .card-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: red;
    opacity: .3; }

  .icon {
    width: 24px;
    height: 24px;
    margin-bottom: 16px;
    img {
      display: block;
      width: 100%;
      height: 100%; } }
  .text {
    margin-bottom: 60px;
    h4 {
      font-size: 18px;
      line-height: 1.5em;
      font-weight: 600;
      margin: 4px; }
    p {
      font-size: 14px;
      line-height: 20px;
      color: #767676;
      margin-bottom: 0; } }
  .btn-white {
    padding: 8px 12px;
    font-size: 14px;
    line-height: 1.5em;
    transition: none; }

  &.yellow {
    &:hover {
      background-color: #FFF7D3;
      border-color: #FFDF4C;
      box-shadow: 4px 4px 8px #FFDF4C33;
      .btn-white {
        background-color: #FFDF4C;
        border-color: #FFDF4C;
        color: #fff; } } }

  &.red {
    &:hover {
      background-color: #F5D9D4;
      border-color: #F73824;
      box-shadow: 4px 4px 8px #F7382433;
      .btn-white {
        background-color: #F73824;
        border-color: #F73824;
        color: #fff; } } }

  &.grey {
    &:hover {
      background-color: #EBEBEB;
      border-color: #9B9B9B;
      box-shadow: 4px 4px 8px #00000033;
      .btn-white {
        background-color: #767676;
        border-color: #767676;
        color: #fff; } } } }

.prjct-card {
  padding: 30px;
  background: #F4D9D3;
  border: 1px solid #F73824;
  box-shadow: 6px 6px 8px #F7382433;
  border-radius: 8px;
  &_title {
    margin-bottom: 40px;
    .icon {
      width: 60px;
      height: 60px;
      display: inline-block;
      vertical-align: middle;
      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain; } }
    .text {
      display: inline-block;
      vertical-align: middle;
      color: $primary;
      width: calc(100% -  77px);
      margin-left: 12px;
      h4 {
        font-size: 22px;
        line-height: 36px;
        font-weight: 700;
        margin-bottom: 4px;
        color: $primary; }
      span,
      b {
        font-size: 14px;
        line-height: 20px;
        display: inline-block; }
      b {
        margin-left: 8px; } } } }


.client-dashboard {}

.proyek-list {
  &_filter {
    margin-bottom: 32px;
    .col-6 {
      flex: 0 0 536px;
      max-width: 536px; }
    .form-search {
      position: relative;
      .btn-primary {
        position: absolute;
        font-size: 14px;
        line-height: 1.5em;
        top: 38px;
        right: 4px; } } } }

.proyek-list {
  &_table {
    table {
      width: 100%;
      margin-bottom: 32px;
      thead,
      tbody {
        display: block; }
      th {
        font-weight: 400;
        color: #A0A0A0;
        font-size: 14px;
        line-height: 1.5em; }
      .prjct-name {
        font-weight: 600;
        font-size: 16px;
        line-height: 1.5em;
        .icon {
          width: 46px;
          height: 46px;
          display: inline-block;
          vertical-align: middle;
          border-radius: 8px;
          margin-right: 14px;
          overflow: hidden;
          img {
            display: block;
            width: 100%;
            height: 100%; } }
        a {
          color: $primary;
          text-decoration: none; } }
      thead,
      tbody {
        width: 100%;
        tr {
          border-radius: 8px;
          display: flex;
          width: 100%;
          margin-bottom: 32px;
          align-items: center;
          td,
          th {
            height: 100%;
            &:nth-of-type(1) {
              flex-grow: 1; }
            &:nth-of-type(2) {
              flex-grow: 1; }
            &:nth-of-type(3) {
              flex-grow: 1; }
            &:nth-of-type(4) {
              flex-grow: 1; }
            &:nth-of-type(5) {
              flex-grow: 1; } } } }
      tbody {
        tr {
          box-shadow: 4px 4px 8px #6C6C6C0F;
          background: #fff;
          padding: 14px;
          transition: all .3s ease;
          border: 1px solid #EDEDED;
          &:hover {
            cursor: pointer;
            border-color: $primary; } }
        td {
          font-size: 14px;
          line-height: 1.5em;
          &:first-child {
            border-radius: 8px 0 0 8px; }
          &:last-child {
            border-radius: 0 8px 8px 0; }
          .progress-bar {
            width: 200px;
            .pb {
              p {
                font-size: 14px;
                line-height: 1em;
                margin-bottom: 5px; }
              .pb-block {
                background: #F4D9D3;
                height: 8px;
                span {
                  height: 8px; } } } } } } } } }

// Pagination
.pagination {
  ul {
    margin: 0 auto;
    // margin-top: 30px
    li {
      display: inline-block;
      vertical-align: middle;
      a {
        color: $text;
        display: inline-block;
        vertical-align: middle;
        padding: 6px 16px;
        border-radius: 8px;
        text-decoration: none; }
      &.active {
        a {
          background: $primary;
          color: #fff; } } } } }

.section_title {
  &.light {
    h3 {
      font-weight: 400; } } }


// Client brief
.client-brief {
  .brief {
    padding-bottom: 40px;
    border-bottom: 1px solid #EDEDED;
    .card-white {
      min-height: 296px; }
    &:last-child {
      border-bottom: 0; }
    .section_title {
      h3 {
        font-weight: 400; } } } }

.client-pembayaran {
  .proyek-list_filter {
    .dropdown.bootstrap-select {
      .btn.dropdown-toggle {
        width: auto; } } } }

// Pembayaran List
.pembayaran-list {
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid #EDEDED;
  table {
    margin-bottom: 0;
    thead {
      tr {
        margin-bottom: 16px; } }
    tbody {
      tr {
        padding: 22px;
        margin-bottom: 16px;
        &:last-of-type() {
          margin-bottom: 0; }
        &:hover {
          border-color: #1CEA6F; } }
      td {
        .price {
          font-weight: 600;
          color: #1CEA6F; }
        .lunas {
          color: #1CEA6F; } } } }
  &.notpaid {
    table {
      tbody {
        tr {
          &:hover {
            border-color: #F23C13; } }
        td {
          .price {
            color: #F23C13; }
          .lunas {
            color: #F23C13; } } } } } }

// Write brief
.write-brief {
  .page-title {
    margin-bottom: 60px;
    h2 {
      font-size: 30px;
      line-height: 35px;
      margin-bottom: 12px;
      color: $primary; } }

  .steps {
    max-width: 630px;
    margin-bottom: 80px;
    ul {
      display: flex;
      li {
        display: inline-block;
        position: relative;
        flex: 0 0 calc(100% / 3);
        max-width: calc(100% / 3);
        u {
          text-decoration: none;
          font-size: 18px;
          line-height: 30px;
          color: #FFF;
          font-weight: 500;
          display: inline-block;
          margin-bottom: 8px;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          background: #D5D5D5;
          text-align: center; }
        span {
          display: block;
          font-size: 14px;
          line-height: 20px;
          font-weight: 600;
          color: #D5D5D5; }
        &.active {
          u {
            background: $primary; }
          span {
            color: $primary; } }
        & + li {
          &:before {
            content: "";
            width: 180px;
            height: 3px;
            display: block;
            background: #D5D5D5;
            position: absolute;
            top: 13px;
            left: calc(-100% + 30px); } }
        &.active + li {
          &:before {
            background: $primary; } } } } }

  .form {
    max-width: 630px; } }

// Write Brief Confirm
.write-brief-confirm {
  text-align: center;
  .brief-confirm {
    &__img {
      margin: 0 auto 55px;
      max-height: 300px;
      img {
        display: block;
        margin: 0 auto;
        max-height: 300px; } }
    &__text {
      margin-bottom: 48px;
      h2 {
        font-size: 30px;
        line-height: 1.2em;
        margin-bottom: 10px;
        color: $primary; }
      p {
        font-size: 18px;
        line-height: 1.5em;
        span {
          color: $primary; } } } } }

.modal-brief-cancelConfirm {
  .modal {
    &-content {
      position: relative;
      .close {
        position: absolute;
        top: 24px;
        right: 14px;
        z-index: 9; } }
    &-body {
      padding: 55px 30px; } }
  .cancelConfirm {
    text-align: center;
    &__img {
      img {
        display: block;
        margin: 0 auto 36px; } }
    &__text {
      text-align: center;
      h3 {
        font-size: 30px;
        line-height: 35px;
        font-weight: 500;
        color: $primary;
        margin-bottom: 4px; }
      p {
        font-size: 14px;
        line-height: 1.5em; } }
    &__btn {
      text-align: center;
      margin-top: 24px; } } }


// Contact list
.contact-list {
  // max-width: 628px
  margin-bottom: 116px;

  & > ul {
    margin-top: 40px;
    li {
      &.selected {
        .card-contact {
          border: 1px solid #F23C13;
          background: #F4D9D3; } }
      &:not(:last-child) {
        margin-bottom: 40px; } } } }

.card-contact {
  padding: 20px;
  border: 1px solid #F6F6F6;
  background: #FFFFFF;
  border-radius: 8px;

  &__name {
    font-size: 18px;
    line-height: 1.3em;
    font-weight: 500;
    color: $primary;
    margin-block-end: 8px;
    position: relative;
    .slctd {
      position: absolute;
      top: 0;
      right: 0;
      font-size: 14px;
      line-height: 16px;
      font-weight: 500;
      color: $primary; }
    span {
      u.tag {
        font-size: 10px;
        line-height: 12px;
        color: #FFF;
        padding: 3px 6px;
        border-radius: 4px;
        background: $primary;
        display: inline-block;
        vertical-align: middle;
        margin-left: 8px; } } }
  &__detail {
    margin-bottom: 32px;
    ul {
      li {
        &:not(:last-child) {
          margin-bottom: 4px; } } } }
  &__action {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    .btn-link {
      font-size: 14px;
      line-height: 1.5em;
      font-weight: 400;
      color: $primary;
      text-decoration: none;
      & + .btn-link {
        margin-left: 24px; } } } }

.modal-add-rincian-contact {
  max-width: 550px;
  .modal {
    &-content {
      position: relative;
      .close {
        position: absolute;
        top: 24px;
        right: 14px; } }
    &-title {
      margin-bottom: 32px;
      h3 {
        font-size: 18px;
        line-height: 1.5em;
        font-weight: 500;
        margin-bottom: 0; } }
    &-body {
      padding: 80px 0;
      max-width: 352px;
      margin: 0 auto;
      .form-control,
      .dropdown.bootstrap-select .btn.dropdown-toggle {
        height: 42px; }

      .form-action {
        padding-top: 50px;
        .btn-primary {
          font-weight: 400; } } } } }

.modal-addcontact-notif {
  .modal {
    &-body {
      padding: 70px 30px;
      p {
        text-align: center;
        font-size: 20px;
        line-height: 1.5em; }
      .form-action {
        margin-top: 45px;
        text-align: center; } } } }


